/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { usePortal } from "Hooks"
import Product from "../../products/product"
import Pagination from "../../pagination"
import { removeSkelly } from "Utils"

const Notice = wp.element.lazy(() =>
  import(/* webpackChunkName: 'Notice-public' */ "../../notice")
)

function StorefrontItems({ noResultsText, settings, payload, isLoading }) {
  const { useEffect, useState } = wp.element
  const [removedSkelly, setRemovedSkelly] = useState(false)
  const noticeCSS = css`
    && {
      margin-left: 40px;
      margin-top: 0;
    }
  `

  const storefrontItemsWrapperCSS = css`
    position: relative;
  `

  useEffect(() => {
    if (payload && !removedSkelly) {
      removeSkelly(document.getElementById("shopwp-storefront"))
      setRemovedSkelly(true)
    }
  }, [payload])

  return usePortal(
    <div css={storefrontItemsWrapperCSS}>
      {payload.length || isLoading ? (
        <Pagination>
          <Product />
        </Pagination>
      ) : (
        <Notice css={noticeCSS} status="info">
          {noResultsText}
        </Notice>
      )}
    </div>,
    document.querySelector(settings.dropzonePayload)
  )
}

export default StorefrontItems
